export default {
  name: 'pickupitem',
  props: {
    item: {
      type: Object,
      default: ''
    }
  },
  computed: {
  },
  data () {
    return {
    }
  },
  created () {
  },
  methods: {
    goPath (item) {
      this.$router.push({path: '/pickup/detail', query: item})
    },
    watchOffice (userId) {
      this.$router.push({path: '/office/location', query: {userId}})
    }
  }
}




