
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapActions, mapGetters } from 'vuex'

export default {
  name: 'app',
  created () {
  },
  data () {
    return {
    }
  },
  components: {
    Loading
  },
  computed: {
    ...mapState({
      isLoading: state => state.isLoading,
      isJump: state => state.isJump,
      jumpSrc: state => state.jumpSrc
    }),
    ...mapGetters({
      'openid': 'getOpenId'
    })
  },
  methods: {
    ...mapActions([
      'setOpenid',
      'setUserInfo'
    ])
  }
}



