import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'vedioadd',
  async created () {
    if (!this.openid) {
      return this.$router.push({path: '/init', query: {page: 1}})
    }
    const res = await this.initVedios()
    if (res.type !== 'success') {
      this.$vux.toast.show(res)
    }
  },
  mounted () {
    window.document.title = '视频列表'
  },
  computed: {
    ...mapGetters({
      vedios: 'getVedios',
      openid: 'getOpenId'
    })
  },
  data () {
    return {
    }
  },
  methods: {
    ...mapActions([
      'initVedios'
    ])
  }
}



