import { mapActions } from 'vuex'

export default {
  name: 'senditem',
  props: {
    item: {
      type: Object,
      default: {}
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
  },
  data () {
    return {
    }
  },
  created () {
    // console.log('item', this.item)
  },
  methods: {
    ...mapActions([
      'setSend',
      'cancleSend'
    ]),
    showToast (data) {
      this.$vux.toast.show({
        text: data.text || '出错啦',
        type: data.type || 'warn',
        width: data.width || '20rem'
      })
    },
    goPath (item, type) {
      console.log('irwem', item)
      const id = item.id
      const order = item.order
      this.$router.push({path: '/send/qr', query: {id, order}})
    },
    showOffice ({province = '', city = '', district = '', descript = ''}) {
      const content = province + city + district + descript
      this.showToast({
        text: content,
        type: 'text',
        width: '25rem'
      })
    },
    async cancle (item) {
      const _this = this // 需要注意 onCancel 和 onConfirm 的 this 指向
      this.$vux.confirm.show({
        title: '确定取消这一订单吗?',
        onCancel () {
        },
        async onConfirm () {
          const res = await _this.cancleSend({
            id: item.id,
            type: 5})
          _this.showToast(res)
        }
      })
    },
    watchOffice (userId) {
      this.$router.push({path: '/office/location', query: {userId}})
    }
  }
}




