export default {
  created () {
    let {code, state} = this.$route.query
    if (!code && !state) {
      console.log('Redirect.vue: no code fail')
    } else {
      window.localStorage.removeItem('mj_code')
      window.localStorage.setItem('mj_code', code)
      this.$router.push({path: '/init', query: {code, page: state}})
    }
  },
  data () {
    return {
    }
  }
}



