import { mapActions } from 'vuex'

export default {
  name: 'vedioitem',
  props: {
    item: {
      type: Object,
      default: {}
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
  },
  data () {
    return {
    }
  },
  created () {
  },
  methods: {
    ...mapActions([
    ]),
    showToast (data) {
      this.$vux.toast.show({
        text: data.text || '出错啦',
        type: data.type || 'warn',
        width: data.width || '20rem'
      })
    },
    cancle (item) {
    },
    goPath (href) {
      const reg = /htt/g
      if (!reg.test(href)) {
        this.showToast({
          text: '视频链接出错',
          type: 'warn',
          width: '18rem'
        })
        return
      }
      window.location.href = href
    }
  }
}




