
import XInput from 'vux/src/components/x-input/index.vue'

import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'vedioadd',
  components: {
    XInput
  },
  computed: {
    ...mapGetters({
      openid: 'getOpenId'
    })
  },
  created () {
    if (!this.openid) {
      return this.$router.push({path: '/init', query: {page: 1}})
    }
  },
  mounted () {
    window.document.title = '添加视频'
  },
  data () {
    return {
      title: '',
      href: '',
      pwd: ''
    }
  },
  methods: {
    ...mapActions([
      'addVedio'
    ]),
    async saveVedio () {
      if (!this.title || !this.href) {
        this.$vux.toast.show({
          text: '请将信息填写完整',
          type: 'warn',
          width: '18rem'
        })
        return
      }
      const create = {
        title: this.title,
        href: this.href,
        pwd: this.pwd
      }
      const res = await this.addVedio({create})
      this.$vux.toast.show(res)
      if (res.type === 'success') {
        this.$router.push({path: '/vedio/list'})
      }
    }
  }
}



