import window from 'window'

export default {
  name: 'header',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isIndex: false
    }
  },
  created () {
    const path = this.$route.path
    if (path === '/') {
      this.isIndex = true
    }
  },
  methods: {
    goBack () {
      const path = this.$route.path
      if (path !== '/' && window.history.length === 1) {
        this.$router.push({path: '/'})
      } else {
        this.$router.go(-1)
      }
    },
    goIndex () {
      const path = this.$route.path
      const that = this
      if (path !== '/index' && path !== '/') {
        that.$vux.confirm.show({
          title: '是否返回主页?',
          onCancel () {},
          onConfirm () {
            that.$router.push({path: '/'})
          }
        })
      }
    }
  }
}



